/* import fontov */
@import url("https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300;400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Space+Mono:wght@700&display=swap");

/* resetovanie základných štýlov */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* deinovanie premennych */
:root {
  --accent-color: #f87070;
  --background-body: #1e213f;
  --text: #d7e0ff;
  --text-dark: #1e213f;
  --background-preferences: #ffffff;
  --input-background: #eff1fa;
  --background-timer: #161932;
  --settings-heading: #161932;
  --default-accent: #f87070;
  --blue-accent: #70f3f8;
  --purple-accent: #d881f8;

  --font-current: "Kumbh Sans", sans-serif;
  --font-default: "Kumbh Sans", sans-serif;
  --font-roboto-slab: "Roboto Slab", serif;
  --font-space-mono: "Space Mono", monospace;
}

/* základné rozvrhnutie tela stránky */
html,
body {
  height: 100%; 
}

body {
  display: flex;
  justify-content: center;
  background: var(--background-body);
}

/* hlavný wrapper aplikácie */
.pomodoro-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 667px;
  font-family: var(--font-current);
}

/* nadpis */
h1 {
  font-family: "Kumbh Sans", sans-serif;
  color: var(--text);
  text-align: center;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  margin-top: 32px;
}

/* obal ovládacích tlačidiel */
.controls {
  display: flex;
  justify-content: space-between;
  background: var(--background-timer);
  border-radius: 31.5px;
  width: 327px;
  min-height: 63px;
  padding: 8px 6px;
  margin: 45px 0 48px 0;
  z-index: 100;
}

/* skrytie inputov pre vlastné štýlovanie */
.controls input[type="radio"] {
  opacity: 0;
  position: fixed;
  width: 0;
}

/* tlačidlá prepínania módov */
.controls__button {
  display: flex;
  align-items: center;
  height: 48px;
  border: none;
  border-radius: 26.5px;
  background: var(--background-timer);
  padding: 0 15px;

  font-family: var(--font-current);
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  text-align: center;
  color: var(--text);
  mix-blend-mode: normal;
  opacity: 0.4;
  cursor: pointer;
}

/* štýl aktívneho tlačidla */
.controls input[type="radio"]:checked + label {
  color: var(--text-dark);
  opacity: 1;
  background: var(--accent-color);
  animation-name: fade;
  animation-timing-function: ease-in;
  animation-duration: 0.05s;
}

/* hlavný timer */
.timer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 300px;
  height: 300px;
  border-radius: 90%;
  background: linear-gradient(deg, #0e112a 100%);
  box-shadow: -50px -50px 100px #272c5a, 50px 50px 100px #272c5a;
  cursor: pointer;
  margin-bottom: 20px;
}

/* zmenšenie pri kliknutí */
.timer:active {
  transform: scale(0.98);
}

/* vnútorný kruh timeru */
.timer__display {
  height: 89.26%;
  width: 89.26%;
  padding: 11px;
  border-radius: 90%;
  background: var(--background-timer);
}

/* tlačidlo pre stíšenie */
button.display__mute {
  width: 25px;
  height: 25px;
  border: 0;
  background-color: transparent;
  color: #d7e0ff2f;
  transition: color 0.3s ease-in-out;
  cursor: pointer;
}

/* zmena farby pri hover */
button.display__mute:hover {
  color: #d7e0ff;
}

/* skrytie outlines */
button:focus:not(:focus-visible) {
  outline: 0;
}

/* tlačidlo štartu/pauzy */
.display__start-pause {
  margin-top: 50%;
  font-family: var(--font-default);
  font-weight: bold;
  font-size: 16px;
  background-color: var(--background-timer);
  border: 0;
  color: var(--text);
  line-height: 16px;
  letter-spacing: 15px;
  margin-right: -15px;
  transition: transform 0.2s ease-in-out;
  cursor: pointer;
}

/* ikona nastavení */
.pomodoro-app__preferences {
  margin-top: 79px;
  background: none;
  border: 0;
  transition: transform 0.15s ease-in-out;
  cursor: pointer;
}

/* animácia pri hover */
.pomodoro-app__preferences:hover {
  transform: scale(1.4) rotate(45deg);
}

/* tmavé pozadie modálneho okna */
.preferences {
  z-index: 200;
  position: fixed;
  display: none;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}

/* jednoduchá fade animácia */
@keyframes fade {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}

/* zobrazenie preferencií */
.preferences--visible {
  display: block;
  animation-name: fade;
  animation-timing-function: ease-in;
  animation-duration: 0.15s;
}

/* samotný panel preferencií */
.preferences__pane {
  top: 46px;
  width: 327px;
  height: 549px;
  background: var(--background-preferences);
  border-radius: 15px;
  position: relative;
  margin: -70px auto auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2),
              0 6px 20px rgba(0, 0, 0, 0.19);
  transition: 3.25s all ease-in-out;
}

/* nadpis panelu */
.preferences__pane h2 {
  padding: 24px 0 28px 24px;
  border-bottom: 1px solid #e3e1e1;
  font-family: var(--font-default);
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  color: var(--settings-heading);
}

/* tlačidlo na zatvorenie panelu */
.pane__close-preferences {
  border: none;
  background: none;
  position: absolute;
  right: 24px;
  top: 19px;
  color: var(--background-body);
  opacity: 0.5;
  font-size: 24px;
  transition: transform 0.15s ease-in-out;
}

.pane__close-preferences:hover {
  transform: scale(1.65);
  cursor: pointer;
}

/* nadpis sekcií */
h3 {
  width: 100%;
  text-align: center;
  font-family: var(--font-default);
  font-weight: bold;
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 4.23077px;
  color: var(--settings-heading);
}

/* sekcie v paneli */
.pane__time-settings,
.pane__color-preference {
  margin-top: 28px;
}

/* rozloženie časových inputov */
.time-settings__form {
  margin: 18px 24px 24px 23px;
  display: grid;
  grid-template-columns: 50% 50%;
  align-items: center;
  row-gap: 8px;
}

/* štýly labelov */
label {
  font-family: var(--font-default);
  font-weight: bold;
  font-size: 12px;
  color: var(--text-dark);
  opacity: 0.4;
}

/* štýl vstupných polí */
input {
  width: 140px;
  height: 40px;
  padding-left: 16px;
  border: none;
  border-radius: 10px;
  background: var(--input-background);
  font-weight: bold;
  font-size: 14px;
  color: var(--text-dark);
}

/* štýlovanie číslicových inputov */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  margin-right: 16px;
  -webkit-appearance: none;
  background: url("data:image/svg+xml,...") no-repeat center center;
  height: 21px;
  width: 14px;
  opacity: 0.5;
  cursor: pointer;
}

input[type="number"]::-webkit-inner-spin-button:hover,
input[type="number"]::-webkit-inner-spin-button:active {
  opacity: 1;
}

/* skrytie inputov pre výber farby/písma */
.pane__color-preference input[type="radio"],
.pane__font-preference input[type="radio"] {
  opacity: 0;
  position: fixed;
  width: 0;
}

/* vzhľad labelov ako tlačidiel */
.pane__color-preference input[type="radio"] + label,
.pane__font-preference input[type="radio"] + label {
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.25s, background-color 0.25s;
  cursor: pointer;
}

/* zväčšenie pri hover */
.pane__color-preference input[type="radio"] + label:hover,
.pane__font-preference input[type="radio"] + label:hover {
  transform: scale(1.1);
}

/* obal volieb farby/písma */
.pane__font-preference,
.pane__color-preference {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 24px 0;
  margin: 0 24px;
  border-style: solid;
  border-width: 1px 0;
  border-color: #e3e1e1;
}

.pane__color-preference {
  border: none;
}

/* tlačidlá výberu */
.font-preference__kumbh,
.font-preference__roboto,
.font-preference__space,
.color-preference__default,
.color-preference__blue,
.color-preference__purple {
  width: 40px;
  height: 40px;
  margin: 18px 8px 0;
  border-radius: 90%;
  font-size: 15px;
}

/* štýly font tlačidiel */
.font-preference__kumbh,
.font-preference__roboto,
.font-preference__space {
  background: var(--input-background);
  color: var(--text-dark);
}

.pane__font-preference input[type="radio"]:checked + label {
  background: var(--settings-heading);
  color: #fff;
}

.pane__font-preference input[type="radio"]:hover + label {
  background: var(--settings-heading);
  color: #fff;
  opacity: 0.9;
}

/* konkrétne fonty */
.font-preference__kumbh {
  font-family: var(--font-default);
  font-weight: bold;
  padding-top: 5px;
}

.font-preference__roboto {
  font-family: var(--font-roboto-slab);
}

.font-preference__space {
  font-family: var(--font-space-mono);
  font-weight: bold;
}

/* konkrétne farby */
.color-preference__default {
  background-color: var(--default-accent);
}
.color-preference__blue {
  background-color: var(--blue-accent);
}
.color-preference__purple {
  background-color: var(--purple-accent);
}

/* vzhľad aktívnej farby */
.pane__color-preference input[type="radio"]:checked + label {
  background-image: url("data:image/svg+xml,...");
  background-repeat: no-repeat;
  background-position: center;
}

.pane__color-preference input[type="radio"]:hover + label {
  background-image: url("data:image/svg+xml,...");
  background-repeat: no-repeat;
  background-position: center;
}

/* obal pre potvrdenie */
.pane__apply-row {
  width: 100%;
  display: flex;
  justify-content: center;
}

/* tlačidlo potvrdenia */
.pane__apply-preferences {
  width: 140px;
  height: 53px;
  margin-top: 36px;
  border-radius: 26.5px;
  background-color: var(--accent-color);
  font-family: Kumbh Sans;
  font-weight: bold;
  font-size: 16px;
  color: #ffffff;
  transition: transform 0.15s ease-in-out;
  cursor: pointer;
}

.pane__apply-preferences:hover {
  transform: scale(1.1);
}

/* responzivita pre tablety */
@media screen and (min-width: 600px) {
  h1 {
    font-size: 32px;
    margin-top: 48px;
  }

  .pomodoro-app {
    height: 100%;
  }

  .controls {
    width: 373px;
    margin: 55px 0 45px;
  }

  .controls__button {
    font-size: 14px;
  }

  .timer {
    width: 410px;
    height: 410px;
  }

  .pane__close-preferences {
    right: 36px;
    top: 39px;
    font-size: 28px;
  }

  .preferences__pane {
    top: 267px;
    width: 540px;
    height: 490px;
    border-radius: 25px;
    margin-bottom: 40px;
  }

  .preferences__pane h2 {
    padding: 34px 0 31px 40px;
    font-size: 28px;
  }

  .time-settings__form {
    margin: 24px 40px;
  }

  .pane__font-preference,
  .pane__color-preference {
    margin: 0 40px;
  }

  .pane__time-settings h3 {
    margin: 24px 40px 22px;
  }

  .pane__font-preference h3,
  .pane__color-preference h3 {
    width: 300px;
    margin-top: 18px;
  }

  .time-settings__form {
    grid-template-columns: auto auto auto;
    grid-template-rows: 12px 48px;
    column-gap: 22px;
  }

  label {
    grid-row: 1;
  }

  input {
    grid-row: 2;
  }
}

/* ďalšie rozloženie pre väčšie displeje */
@media screen and (min-width: 769px) {
  .preferences__pane {
    top: 155px;
  }
}
